/* src/Components/BottomSection.module.css */

.bottomSection {
  background-color: white;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-top: 2px solid #333;
  position: relative;
  bottom: 0;
  width: 120%;
  margin-bottom: 0px; /* Add margin below the bottom section */
}

.logoContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 25px; /* Adjust the vertical placement */
  width: 50%; /* Set the width of the container to 50% */
  margin-left: -43%; /* Center the container horizontally */
}

.icon {
  width: 60px; /* Adjust the size of the icons */
  height: 60px;
  margin: 0 10px; /* Adjust the horizontal placement */
}

.linksContainer {
  display: flex;
  justify-content: flex-start; /* Move links to the right */
  gap: 10px;
  margin-top: -85px; /* Move links up */
  margin-left: 25%; /* Move links to the right */
}

.link {
  color: #333;
  text-decoration: none;
  font-size: 16px;
  font-weight: bold;
}

.link:hover {
  text-decoration: underline;
}

.copyRight {
  font-size: 14px;
  color: #333;
  margin-top: 10px; /* Adjust the vertical placement if needed */
  margin-left: 25%;
}