.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7); /* Darken the background overlay */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .popup {
    background-color: white;
    padding: 30px; /* Increased padding for a more spacious feel */
    border-radius: 15px; /* Slightly more rounded corners */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5); /* Deeper shadow for better pop */
    width: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center; /* Center align text */
  }
  
  .closeButton {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
  }
  
  .congratsText {
    font-size: 2.5rem; /* Larger font for a more impactful message */
    color: #0c0c0c; /* Green color for success */
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .playerName {
    font-size: 2rem;
    color: #0c0c0c; /* Keeping the blue theme */
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .imageContainer {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 20px; /* Space between image and text */
  }
  
  .playerImage {
    max-width: 150%; /* Adjust image size slightly for better presentation */
    max-height: 350px;
    border: 2px solid #ff0000; /* Thicker border to highlight the image */
    border-radius: 15px; /* More rounded corners */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Light shadow for depth */
  }
  
  .message {
    font-size: 1.2rem; /* Slightly larger font for better readability */
    color: #333; /* Darker text for contrast */
    margin-top: 20px;
  }
  