.statsButton {
  background-color: green;
  color: white;
  border: 2px solid green;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  font-size: 2rem;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s ease;
  margin-top: 20px;
}

.statsButton:hover {
  background-color: rgb(1, 171, 1); /* Change to a lighter green on hover */
}

.statsIcon {
  font-size: 1.5rem; /* Adjust size if needed */
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  width: 600px;
  z-index: 1000;
  text-align: center;
}

.closeButton {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  border: none;
  background: none;
  font-size: 1.5rem;
}

.statsContainer {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}

.stat {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.statNumber {
  font-size: 2rem;
  font-weight: bold;
}

.statLabel {
  font-size: 1rem;
}

.guessDistribution {
  width: 100%;
}

.guessRow {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px 0;
}

.guessBarContainer {
  flex-grow: 1;
  margin-left: 10px;
}

.guessBar {
  background-color: #ccc;
  text-align: right;
  padding-right: 5px;
  color: black;
  height: 20px;
  line-height: 20px;
  border-radius: 3px;
}
